"use client";

import Navigation from "@/components/ui/navigation"; // Adjust import

export default function AboutPage() {
  return (
    <div className="min-h-screen bg-[#050B16] flex flex-col relative">
      {/* Navbar wrapped in relative container to contain glow */}
      <div className="relative z-10">
        <Navigation />
        
        {/* Glow extending downward */}
        <div
          aria-hidden="true"
          className="absolute left-0 right-0 mx-auto"
          style={{
            top: "100%", // right below navbar
            height: "60px",
            width: "100%",
            maxWidth: "100vw",
            filter: "blur(30px)", 
            background:
              "radial-gradient(ellipse at center, rgba(56,189,248,0.7) 0%, rgba(56,189,248,0) 80%)",
            pointerEvents: "none",
            zIndex: 0,
          }}
        />
      </div>

      {/* Main content section with padding top to not overlap navbar and glow */}
      <main className="flex-1 px-6 max-w-3xl mx-auto pt-32 text-white">
        <h1 className="text-4xl font-extrabold mb-6 text-center">
          About SKYGNOSIS
        </h1>
        <p className="text-lg text-slate-300 leading-relaxed text-center mb-8">
          Weâ€™re a team of innovators, problem-solvers, and technology enthusiasts dedicated to transforming businesses through cutting-edge solutions.
        </p>
        {/* Additional content */}
      </main>
    </div>
  );
}
